.swiper-container {
	width: 100%;
	overflow: hidden;

	background-color: black;
}

.swiper-wrapper {
	display: flex;
	flex-wrap: wrap;
	width: auto;
}

.swiper-slide {
	width: 20%;
}

.swiper-slide img {
	display: block;
	width: 100%;
	max-width: 100%;
}

@media (max-width: 1280px) {
	.swiper-slide {
		width: 25%;
	}
}

@media (max-width: 768px) {
	.swiper-container {
		width: 100%;
		overflow: hidden;
	}

	.swiper-wrapper {
		flex-wrap: nowrap;
		width: 100%;
	}

	.swiper-slide {
		width: 100%;
	}

	.swiper-button-next,
	.swiper-button-prev {
		outline: none;
	}

	.gallery-icon {
		display: none;
	}
}
